@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
:root {
  --background: #ecf2fa;
  --primary: #498dd8;
  --dark: #1d1d20;
}
* {
  box-sizing: border-box;
}
body,
html {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans", sans-serif;
  background-color: #ecf2fa;
  background-color: var(--background);
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
}


.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.welcome {
  height: 100vh;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: opacity 0.5s ease-in-out;
  z-index: 10;
}
.welcome::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--background);
}

.welcome__header__wrapper {
  width: 100%;
  height: 10rem;
  position: relative;
}

.welcome__header,
.welcome__question {
  margin: 0;
  position: absolute;
  font-size: 4.5rem;
  transition: opacity 0.4s ease-in;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}

.welcome__header {
  font-size: 4.5rem;
  transition: opacity 0.4s ease-in;
}

.welcome__question {
  position: absolute;
  font-size: 2.5rem;
  transition: opacity 0.5s 0.5s ease-out;
}
.welcome__image {
  position: relative;
  height: 40%;
}
.welcome__buttons {
  position: relative;
  margin-top: 20px;
}
.welcome__button {
  transition: opacity 0.5s 0.8s ease-out;
  border-radius: 5px;
  outline: none;
  padding: 10px 20px;
  font-size: 20px;
  margin: 10px;
  border: solid 2px var(--primary);
}
.welcome__button:hover,
.welcome__button:focus {
  -webkit-animation: shake 0.2s;
          animation: shake 0.2s;
}
.welcome__button:active {
  transform: scale(0.98);
}
.welcome__button--primary {
  background-color: var(--primary);
  color: var(--background);
}
.welcome__button--secondary {
  color: var(--dark);
}
@-webkit-keyframes shake {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes shake {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0%);
  }
}

.certificate {
  height: 100%;
  width: 100%;
  transition: opacity 0.5s ease-in-out;
  position: relative;
  background-color: var(--background);
}

.certificate__text {
  font-size: 20px;
  fill: var(--dark);
}

.certificate__text--competition {
  font-size: 16px;
}

.form__input {
    width: 100%;
    border-radius: 10px;
    padding: 5px;
    border: solid 2px var(--primary);
    margin-top: 5px;
    font-size: 18px;
}

.form__label {
    width: 20rem;
    max-width: 100%;
    margin-top: 10px;
}
.form {
  position: absolute;
  transition: opacity 0.5s ease-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: var(--background);
}

.form__button {
    width: 20rem;
    padding: 10px;
    margin-top: 30px;
    background-color: var(--primary);
    border: none;
    border-radius: 5px;
    color: var(--background);
    font-size: 24px;
    outline: none;
}

.form__button:hover,
.form__button:focus {
  -webkit-animation: shake 0.2s;
          animation: shake 0.2s;
}
.form__button:active {
  transform: scale(0.98);
}

@-webkit-keyframes shake {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes shake {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0%);
  }
}

.sad {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--background);
  transition: opacity 0.5s ease-out;
}

.sad__image {
  height: 40%;
  margin: 50px 0;
}

